import {useState,useEffect} from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import UserBlocked from '../ReuseableCompononts/UserBlocked';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';

export default function BlockedUserList(props) {
    const [userData, setUserData] = useState([]);
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState();
    const [nextPageUrl, setNextPageUrl] = useState('');

    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const nextData = () => {
        setLoading(false);
        axios
            .get(nextPageUrl, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setUsers(res.data);
                setNextPageUrl(res.data.users.next_page_url);
                console.log('called next page: ', nextPageUrl);
                setUserData(userData.concat(...res.data.users.data));
            });
    };

    const loadUserData = () => {
        setLoading(true);
        axios
            .get('https://server.wakafoods.com/api/admin/user/list/blocked', {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setUsers(res.data.users.data);
                console.log(res.data.users.data);
                setUserData(res.data.users.data);
                setNextPageUrl(res.data.user.next_page_url);
                setLoading(false);
                // if(res.data.products.data.length < 1){
                // 	setStatus('No Ad')
                // }
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };

    useEffect(() => {
        loadUserData();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {userData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No Blocked User Found</b>
                    </p>
                </div>
            ) : (
                <div class="table-responsive">
                    <table class="table">
                        <thead class="thead-light">
                            <tr>
                                <th>Name</th>
                                <th>Title</th>
                                <th>Email</th>
                                <th>Phone</th>
                                <th>Orders Count</th>
                                <th>Action</th>
                            </tr>
                        </thead>
                        <tbody style={{fontSize: '0.7em', width: '50px'}}>
                            {userData.map(data => <UserBlocked data={data} {...props} loadUserData={loadUserData} />)}
                        </tbody>
                        <InfiniteScroll
                            dataLength={userData.length}
                            next={nextData}
                            hasMore={users.current_page !== users.last_page ? true : false}
                            loader={<h4 style={{textAlign: 'center', color: 'gray'}}>Loading...</h4>}
                            endMessage={<p style={{textAlign: 'center'}} />}
                        />
                    </table>
                </div>
            )}
        </div>
    );
}
