import {useState, useEffect} from 'react';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';
import SweetAlert from 'sweetalert2-react';
import {useLocation } from "react-router-dom"

export default function EditCategory(props) {

    const [category, setCategory] = useState({
        name: ''
    });

    const [loading, setLoading] = useState();
    const [Successmessage, setSuccessmessage] = useState({
        status: false, 
        message: ""
    });
    const [Errormessage, setErrorMessage] = useState();
    const [ErrorName, setErrorName] = useState();
    const [show, setShow] = useState(false);
    const [oldCategoryName, setOldCategoryName] = useState('')
    const [categorySlug, setCategorySlug] = useState('')


// TO GET THE STATE WITH REACT-ROUTER FROM THE CATEGORTY TABLE
let location = useLocation();
useEffect(() => {
    setOldCategoryName(location.state.name)
    setCategorySlug(location.state.slug)
}, [])


    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const {name} = category;
    const onChangeHandler = e => {
        setCategory({...category, [e.target.name]: e.target.value});
    };

    const onSubmitHandle = e => {
        e.preventDefault();
        const payload = new FormData();
        payload.append('name', category.name);
        setLoading(true);
        axios
            .post(`https://server.wakafoods.com/api/admin/category/update/${categorySlug}`, payload, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setSuccessmessage({
                    status: true,
                    message: res.data.message
                });
                setLoading(false);
                setTimeout(() => {
                    props.history.push('/category-list');
                }, 2000)
            })
            .catch(error => {
                setErrorMessage(error.response.data.message);
                setErrorName(error.response.data.errors.name);
                console.log(Errormessage);
                setLoading(false);
            });
    };


    
    return (
        <div className="cat-subcat container"> 
            {loading ? <Preloader /> : null}

            {Successmessage.status ? (
                <SweetAlert
                    show={() => setSuccessmessage({
                        ...Successmessage,
                        status: true,
                    })}
                    title="Success message"
                    text={Successmessage.message}
                    onConfirm={() => setSuccessmessage({
                        ...Successmessage,
                        status: false,
                    })}
                />
            ) : null}

            <form style={{marginTop: '30%'}}>
                <div classsName="form-row">
                    <div class="form-group col-md-12">
                        <label style={styles.label} for="inputCity">
                            New Category Name
                        </label>
                        <input
                            class="form-control"
                            type="text"
                            placeholder={oldCategoryName}
                            name="name"
                            value={name}
                            onChange={e => onChangeHandler(e)}
                        />
                        <p style={styles.formError}>
                            {Errormessage !== undefined ? `${Errormessage} ${ErrorName}` : ''}
                        </p>
                    </div>
                    <div class="form-group col-md-12">
                        <button
                            className="btn btn-sm btn-block"
                            style={{backgroundColor: '#ff7417', color: 'white'}}
                            type="submit"
                            onClick={onSubmitHandle}>
                            Update
                        </button>
                    </div>
                </div>
            </form>
        </div>
    )
}

const styles = {
    label: {
        fontSize: '0.9em',
        fontWeight: 'bold'
    },
    formError: {
        fontSize: '0.7em',
        color: 'red',
        marginTop: '10px',
        fontWeight: 'bold'
    }
};