import {useState,useEffect} from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import ActiveLocationView from '../ReuseableCompononts/ActiveLocationView';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';

export default function ActiveLocationList(props) {
    const [stateData,setStateData] = useState([]);
    const [loading, setLoading] = useState();


    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

  

    const  loadLocationData = () => {
        setLoading(true);
        axios
            .get('https://server.wakafoods.com/api/admin/place/list/enabled', {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setStateData(res.data.places);
                setLoading(false);
               
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };


    useEffect(() => {
        loadLocationData();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {stateData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No Location Found</b>
                    </p>
                </div>
            ) : (
            <div class="table-responsive">
                <table class="table">
                    <thead class="thead-light" style={{fontSize: '0.7em', width: '50px'}}>
                        <tr>
                            <th>Name</th> 
                            <th>Pick Up</th> 
                            <th>Delivery</th> 
                            <th className="text-right">Action</th>
                        </tr>
                    </thead>
                    <tbody style={{fontSize: '0.7em', width: '50px'}}>
                    {stateData.map(data => <ActiveLocationView data={data} {...props}  loadLocationData={loadLocationData } />)}
                    </tbody>
                </table>
            </div>
            )}
        </div>
    );
}
