import React from 'react';
import Pic from '../../images/pic.jpg';
import {GiMeal} from 'react-icons/gi';
import {IoMdCheckmarkCircle} from 'react-icons/io';
import {AiOutlineEdit} from 'react-icons/ai';
import {Link} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Naira from 'react-naira';
import axios from 'axios';
import {useSelector} from 'react-redux';

export default function ViewMealDisable(props) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
  //======USER GLOBAL STATE FROM REDUX
  const userSignin = useSelector(state => state.userSignin);
  const {user} = userSignin;


    const enableMeal = () => {
        // setLoading(true);
        axios
            .get(`https://server.wakafoods.com/api/admin/meal/make_available/${props.data.slug}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                props.loadMealData()
                
            })
            .catch(error => {
                console.log(error);
                // setLoading(false);
            });
    };

    return (
        <tr key={props.data.id}>
            <td>{props.data.name}</td>
            <td><Naira>{props.data.price}</Naira></td>
            <td>{props.data.measurement_quantity}</td>
            <td>{props.data.measurement_type}</td>
            <td>
                <div class="btn-group btn-group-sm" role="group" aria-label="...">
                    <button
                        type="button"
                        className="btn btn-primary btn-sm"
                        onClick={handleClickOpen}
                        data-tooltip="tooltip"
                        data-placement="top"
                        title="View meal">
                        <GiMeal />
                    </button>
                    <button
                        type="button"
                        className="btn btn-success btn-sm"
                        data-toggle="tooltip"
                        data-placement="top"
                        title="Disable meal"
                        onClick={() =>  enableMeal ()}
                        >
                        <IoMdCheckmarkCircle/>
                    </button>
                </div>
            </td>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description">
                <DialogTitle id="alert-dialog-title">{props.data.name}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <div className="container-lg ">
                            <div className="block mb-3">
                                <img src={props.data.image} style={{height: '100%', width: '100%'}} />
                            </div>
                            <p>
                                <b>Price</b>:<span className="ml-2">
                                    <Naira>{props.data.price}</Naira>
                                </span>
                            </p>
                            <p>
                                <b>Measurement Quantity</b>{' '}
                                <span className="ml-2">{props.data.measurement_quantity}</span>
                            </p>
                            <p>
                                <b>Measurement Type</b>
                                <span className="ml-2">{props.data.measurement_type}</span>
                            </p>
                            <p>
                                <b>category</b>
                                <span className="ml-2">{props.data.category}</span>
                            </p>
                            <p>
                                <b>subcategory</b>
                                <span className="ml-2">{props.data.subcategory}</span>
                            </p>
                            <div className="mt-3 mb-3 text-center">
                                <h5 style={{color: '#ff7417'}}>Meal Extras</h5>
                            </div>
                            {props.data.extra_items.map(data => (
                                <div>
                                    <p>
                                        <b>Name</b>:<span className="ml-2">{data.name}</span>
                                    </p>
                                    <p>
                                        <b>Price</b>:<span className="ml-2">
                                            <Naira>{data.price}</Naira>
                                        </span>
                                    </p>
                                    <p>
                                        <b>Measurement Quantity</b>{' '}
                                        <span className="ml-2">{data.measurement_quantity}</span>
                                    </p>
                                    <p>
                                        <b>Measurement Type</b>
                                        <span className="ml-2">{data.measurement_type}</span>
                                    </p>
                                    <hr/>
                                </div>
                            ))}
                        </div>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button
                        style={{color: 'white', backgroundColor: '#ff7417'}}
                        onClick={handleClose}
                        color="primary"
                        autoFocus>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </tr>
    );
}

