import {useState,useEffect} from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import BlockedDmanView from '../ReuseableCompononts/BlockedDmanView';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';

export default function BlockedDeliveryManList(props) {
    const [dispatcherData,setDispatcherData] = useState([]);
    const [dispatcher, setDispatcher] = useState([]);
    const [loading, setLoading] = useState();
    const [nextPageUrl, setNextPageUrl] = useState('');

    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const nextData = () => {
        setLoading(false);
        axios
            .get(nextPageUrl, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setDispatcher(res.data);
                setNextPageUrl(res.data.dispatchers.next_page_url);
                console.log('called next page: ', nextPageUrl);
                setDispatcherData(dispatcher.concat(...res.data.dispatchers.data));
            });
    };

    const loadDispatcherData = () => {
        setLoading(true);
        axios
            .get('https://server.wakafoods.com/api/admin/dispatcher/list/blocked', {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setDispatcher(res.data.dispatchers.data);
                console.log(res.data.dispatchers.data);
                setDispatcherData(res.data.dispatchers.data);
                setNextPageUrl(res.data.dispatchers.next_page_url);
                setLoading(false);
                // if(res.data.products.data.length < 1){
                // 	setStatus('No Ad')
                // }
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };

    useEffect(() => {
        loadDispatcherData();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {dispatcherData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No Blocked Delivery Man Found</b>
                    </p>
                </div>
            ) : (
            <div class="table-responsive" >
                <table class="table">
                    <thead class="thead-light" style={{fontSize: '0.7em', width: '50px'}}>
                        <tr>
                            <th>Code</th>
                            <th>Name</th>
                            <th>Location</th>
                            <th>Type</th>
                            <th>Delivery Count</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody style={{fontSize: '0.7em', width: '50px'}}>
                    {dispatcherData.map(data => <BlockedDmanView data={data} {...props} loadDispatcherData={loadDispatcherData} />)}
                    </tbody>
                    <InfiniteScroll
                            dataLength={dispatcherData.length}
                            next={nextData}
                            hasMore={dispatcher.current_page !== dispatcher.last_page ? true : false}
                            loader={<h4 style={{textAlign: 'center', color: 'gray'}}>Loading...</h4>}
                            endMessage={<p style={{textAlign: 'center'}} />}
                        />
                </table>
            </div>
            )}
        </div>
    );
}
