import React from 'react';
import {IoMdCheckmarkCircle} from 'react-icons/io';
import {FaRegUserCircle} from 'react-icons/fa';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import axios from 'axios';
import {useSelector} from 'react-redux';

export default function UserBlocked(props) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const enableUser = () => {
        // setLoading(true);
        axios
            .get(`https://server.wakafoods.com/api/admin/user/activate/${props.data.id}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                props.loadUserData();
            })
            .catch(error => {
                console.log(error);
                // setLoading(false);
            });
    };

    return (
        <tr key={props.data.id}>
            <td>
                <span>{props.data.last_name}</span>
                <span className="m-2">{props.data.first_name}</span>
            </td>
            <td>{props.data.title}</td>
            <td>{props.data.email}</td>
            <td>{props.data.phone}</td>
            <td>{props.data.orders_count}</td>
            <td>
                <div class="btn-group btn-group-sm" role="group" aria-label="...">
                    <button
                        type="button"
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-tooltip="tooltip"
                        data-target="#userProfileModal"
                        data-placement="top"
                        onClick={handleClickOpen}
                        title="View User">
                        <FaRegUserCircle />
                    </button>
                    <button
                        type="button"
                        className="btn btn-success"
                        data-toggle="tooltip"
                        data-placement="top"
                        onClick={() => enableUser()}
                        title="Enable User">
                        <IoMdCheckmarkCircle />
                    </button>
                </div>
            </td>

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description">
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <div className="container-lg ">
                            <div className="block text-center mb-5">
                                <img
                                    src={props.data.avatar}
                                    className="rounded-circle"
                                    style={{height: '200px', width: '200px', objectFit: 'cover'}}
                                />
                                <p className="mt-3" style={{color: 'black', fontSize: '0.9'}}>
                                    <span>{props.data.last_name}</span>
                                    <span className="m-2">{props.data.first_name}</span>
                                </p>
                                <p style={{color: 'black', fontSize: '0.7em'}}>{props.data.email}</p>
                                <p style={{color: 'black', fontSize: '0.7em'}}>{props.data.phone}</p>
                            </div>
                        </div>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button
                        style={{color: 'white', backgroundColor: '#ff7417'}}
                        onClick={handleClose}
                        color="primary"
                        autoFocus>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </tr>
    );
}
