import {useState,useEffect} from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import ActiveChefView from '../ReuseableCompononts/ActiveChefView';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';


export default function ActiveChef(props) {
    const [stateData,setStateData] = useState([]);
    const [chef,setChef] = useState([]);
    const [loading, setLoading] = useState();
    const [nextPageUrl, setNextPageUrl] = useState('');


    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

  

    const  loadData = () => {
        setLoading(true);
        axios
            .get('https://server.wakafoods.com/api/admin/chef/list/active', {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setStateData(res.data.chefs.data);
                setNextPageUrl(res.data.chefs.next_page_url);
                setLoading(false);
               
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };


    const nextData = () => {
        setLoading(false);
        axios
            .get(nextPageUrl, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setChef(res.data);
                setNextPageUrl(res.data.chefs.next_page_url);
                console.log('called next page: ', nextPageUrl);
                setStateData(stateData.concat(...res.data.chefs.data));
            });
    };


    useEffect(() => {
        loadData();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {stateData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No chef Found</b>
                    </p>
                </div>
            ) : (
            <div class="table-responsive">
                <table class="table">
                    <thead class="thead-light" style={{fontSize: '0.7em', width: '50px'}}>
                        <tr>
                            <th>Name</th> 
                            <th>Location</th> 
                            <th>Phone</th> 
                            <th className="text-right">Action</th>
                        </tr>
                        
                    </thead>
                    <tbody style={{fontSize: '0.7em', width: '50px'}}>
                    {stateData.map(data => <ActiveChefView data={data} {...props}  loadData={loadData} />)}
                    </tbody>
                    <InfiniteScroll
                            dataLength={stateData.length}
                            next={nextData}
                            hasMore={chef.current_page !== chef.last_page ? true : false}
                            loader={<h4 style={{textAlign: 'center', color: 'gray'}}>Loading...</h4>}
                            endMessage={<p style={{textAlign: 'center'}} />}
                        />
                </table>
            </div>
            )}
        </div>
    );
}
