import React from 'react';
import {GiCancel} from 'react-icons/gi';
import {AiOutlineEdit} from 'react-icons/ai';
import axios from 'axios';
import {useSelector} from 'react-redux';
import {Link} from 'react-router-dom';

export default function ActiveLocationView(props) {
    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const disableLocation = () => {
        // setLoading(true);
        axios
            .get(`https://server.wakafoods.com/api/admin/place/disable/${props.data.slug}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(() => {
                props.loadLocationData();
            })
            .catch(error => {
                console.log(error);
            });
    };

    return (
        <tr key={props.data.id}>
            <td>{props.data.name}</td>
            {props.data.pickup_available === true ? <td>Yes</td> : <td>No</td>}
            {props.data.delivery_available === true ? <td>Yes</td> : <td>No</td>}

            <td className="text-right">
                <div class="btn-group btn-group-sm" role="group" aria-label="...">
                    <button
                        type="button"
                        className="btn btn-danger"
                        data-toggle="tooltip"
                        data-placement="top"
                        onClick={() => disableLocation()}
                        title="Block State">
                        <GiCancel />
                    </button>
                    <Link
                        style={{color: 'inherit', textDecoration: 'inherit'}}
                        to={{
                            pathname: `/edit-location/${props.data.slug}`,
                            state: props.data
                        }}>
                        <button
                            type="button"
                            className="btn btn-success btn-sm"
                            data-toggle="tooltip"
                            data-placement="top"
                            title="Edit Location">
                            <AiOutlineEdit />
                        </button>
                    </Link>
                </div>
            </td>
        </tr>
    );
}
