import React, {useState, useEffect} from 'react';
import 'react-quill/dist/quill.snow.css';
import ReactQuill from 'react-quill';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';
import SweetAlert from 'sweetalert2-react';
import jsonToFormData from 'json-form-data';

const CreateMeals = props => {
    const [errors, setError] = useState({});
    const [loading, setLoading] = useState();
    const [Successmessage, setSuccessmessage] = useState({
        status: false,
        message: ''
    });
    const [mealData, setMealtData] = useState({
        name: '',
        price: 0,
        category: '',
        subcategory: '',
        measurement_quantity: 0,
        measurement_type: '',
        description: '',
        image: ''
    });

    const newMealExtraData = {
        name: '',
        price: 0,
        measurement_type: '',
        measurement_quantity: 0
    };

    const [inputs, setInputs] = useState([]);

    //====== FOR ADD MEAL EXTRA========
    const addNewExtraData = e => {
        e.preventDefault();
        let tempInputs = [...inputs];
        tempInputs.push(newMealExtraData);
        setInputs(tempInputs);
        console.log(inputs);
    };

    //======ONCHANGE FOR MEAL EXTRA========
    const onChange = (e, index) => {
        let oldInput = [...inputs];
        console.log(oldInput[index]);
        oldInput[index][e.target.name] = e.target.value;
        setInputs(oldInput);
    };

    const {subcategory, category, name, measurement_quantity, price, measurement_type, description, image} = mealData;

    //======ONCHANGE FOR OTHER INPUTS========
    const onChangeHandler = e => {
        setMealtData({...mealData, [e.target.name]: e.target.value});
    };

    //======ONCHANGE FOR IMAGE========
    const onChangeImageHandler = e => {
        setMealtData({...mealData, image: e.target.files[0]});
    };

    //======ONCHANGE FOR CATEGORY TO AVOID TO RECALL THE SUBCATECORY AGAIN===========
    const onCategoryChange = e => {
        setMealtData({...mealData, category: e.target.value});
        loadSubCategory(e.target.value);
    };

    // ===ONCHANGE FOR DESCRIPTION ==========
    const onDescriptionChange = val => {
        setMealtData({...mealData, description: val});
    };

    // ========== API CALL FOR THE CATEGORY LIST =======
    const [categoryData, setCategoryData] = useState([]);
    const loadCategory = () => {
        axios
            .get('https://server.wakafoods.com/api/admin/category/list', {
               
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setCategoryData(res.data.categories);
            })
            .catch(error => {
                console.log(error);
            });
    };
    useEffect(() => {
        loadCategory();
    }, []);

    // ==============SUBCATEGORY LIST STATE =========

    const [subcategoryData, setSubCategoryData] = useState([]);
    const [notShow, setNotShow] = useState(true);
    const loadSubCategory = category => {
        axios
            .get(`https://server.wakafoods.com/api/admin/subcategory/list/${category}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                console.log(res);
                setSubCategoryData(res.data.subcategories);
                setNotShow(false);
            })
            .catch(error => {
                console.log(error);
            });
    };

    //======USER GLOBAL STATE FROM REDUX============
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;


     // ======SUBMIT FUNC ===========
    const onSubmitHandle = e => {
        e.preventDefault();
        let ad_form_data = {...mealData, meal_extras: inputs};
        console.log(ad_form_data);
        const form_config = {
            initialFormData: new FormData(),
            showLeafArrayIndexes: true,
            includeNullValues: false,
            mapping: function(value) {
                if (typeof value === 'boolean') {
                    return +value ? '1' : '0';
                }
                return value;
            }
        };
        const main_form_data = jsonToFormData(ad_form_data, form_config);

        setLoading(true);
        console.log(inputs);

        axios
            .post('https://server.wakafoods.com/api/admin/meal/new', main_form_data, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setSuccessmessage({
                    status: true,
                    message: res.data.message
                });
                setLoading(false);
                setTimeout(() => {
                    props.history.push('/active-meal-list');
                }, 2000);
            })
            .catch(error => {
                setError(error.response.data);
                setLoading(false);
            });
    };

    return (
        <div className="making-page-centerelized container">
            {loading ? <Preloader /> : null}
            {Successmessage.status ? (
                <SweetAlert
                    show={() =>
                        setSuccessmessage({
                            ...Successmessage,
                            status: true
                        })}
                    title="Success message"
                    text={Successmessage.message}
                    onConfirm={() =>
                        setSuccessmessage({
                            ...Successmessage,
                            status: false
                        })}
                />
            ) : null}
            <form className="mt-5">
                <div class="form-row">
                    <div class="form-group col-md-6">
                        <label style={styles.label} for="inputState">
                            Category
                        </label>
                        <select
                            name="category"
                            onChange={e => onCategoryChange(e)}
                            id="inputState"
                            class="form-control"
                            style={{fontSize: '0.7em'}}>
                            <option hidden>
                                {'>>>>'} Choose Category {'<<<<'}
                            </option>
                            {categoryData.map(data => (
                                <option
                                    className="cursor"
                                    key={data.slug}
                                    value={data.slug}
                                    selected={category === data.slug ? true : false}>
                                    {data.name}
                                </option>
                            ))}
                        </select>
                        {errors.errors && <p style={styles.formError}>{errors.errors.category}</p>}
                    </div>
                    <div class="form-group col-md-6">
                        <label style={styles.label} for="inputState">
                            Sub Category
                        </label>
                        <select
                            style={{fontSize: '0.7em'}}
                            className="form-control"
                            name="subcategory"
                            value={subcategory}
                            onChange={e => onChangeHandler(e)}
                            disabled={notShow}>
                            <option hidden>
                                {'>>>>'} select subcategory {'<<<<'}
                            </option>
                            {subcategoryData.map(data => (
                                <option
                                    className="cursor"
                                    key={data.slug}
                                    value={data.slug}
                                    selected={subcategory === data.slug ? true : false}>
                                    {data.name}
                                </option>
                            ))}
                        </select>
                        {errors.errors && <p style={styles.formError}>{errors.errors.subcategory}</p>}
                    </div>
                    <div class="form-group col-md-6">
                        <label style={styles.label} for="inputCity">
                            Meal Name
                        </label>
                        <input
                            type="text"
                            placeholder="Enter Meal Name"
                            class="form-control"
                            name="name"
                            value={name}
                            onChange={e => onChangeHandler(e)}
                        />
                         {errors.errors && <p style={styles.formError}>{errors.errors.name}</p>}
                    </div>
                    <div class="form-group col-md-6">
                        <label style={styles.label} for="inputCity">
                            Meal Price
                        </label>
                        <input
                            type="text"
                            placeholder="Enter Meal Price"
                            class="form-control"
                            name="price"
                            value={price}
                            onChange={e => onChangeHandler(e)}
                        />
                         {errors.errors && <p style={styles.formError}>{errors.errors.price}</p>}
                    </div>
                    <div class="form-group col-md-6">
                        <label style={styles.label} for="inputCity">
                            Measurement Quantity
                        </label>
                        <input
                            type="number"
                            min="1"
                            placeholder="Enter Measurement Quantity"
                            class="form-control"
                            name="measurement_quantity"
                            value={measurement_quantity}
                            onChange={e => onChangeHandler(e)}
                        />
                         {errors.errors && <p style={styles.formError}>{errors.errors.measurement_quantity}</p>}
                    </div>
                    <div class="form-group col-md-6">
                        <label style={styles.label} for="inputState">
                            Measurement Type
                        </label>
                        <select
                            name="measurement_type"
                            value={measurement_type}
                            onChange={e => onChangeHandler(e)}
                            id="inputState"
                            class="form-control"
                            style={{fontSize: '0.7em'}}>
                            <option hidden selected>
                                Choose
                            </option>
                            <option value="g">Gram</option>
                            <option value="kg">Kilogram</option>
                            <option value="cube">Cube</option>
                            <option value="piece">Piece</option>
                            <option value="small">Small</option>
                            <option value="big">Big</option>
                            <option value="medium">Medium</option>
                            <option value="large">Large</option>
                            <option value="xlarge">XLarge</option>
                            <option value="xxlarge">XXLarge</option>
                            <option value="jumbo">Jumbo</option>
                        </select>
                        {errors.errors && <p style={styles.formError}>{errors.errors.measurement_type}</p>}
                    </div>

                    <div className="mt-5 mb-2 text-center" style={{color: '#ff7417'}}>
                        <h6 className="text-center block form-group col-md-12">
                            <b>Meal Extras</b>
                        </h6>
                    </div>

                    {inputs.map((value, i) => (
                        <div key={`meal_extra_${i}`}>
                            <div className="from-group col-md-12 w-100">
                                <div class="form-row">
                                    <div className="from-group col-md-3">
                                        <label style={styles.label} for="inputState">
                                            Name
                                        </label>
                                        <input
                                            type="text"
                                            class="form-control"
                                            placeholder="Enter Extra Name"
                                            name="name"
                                            value={inputs[i].name}
                                            onChange={(e, index = i) => onChange(e, index)}
                                        />
                                        {errors.errors && <p style={styles.formError}>{errors.errors.meal_extras}</p>}
                                    </div>
                                    <div className="from-group col-md-3">
                                        <label style={styles.label} for="inputState">
                                            Price
                                        </label>
                                        <input
                                            type="text"
                                            placeholder="Enter Extra Price"
                                            class="form-control"
                                            name="price"
                                            value={inputs[i].price}
                                            onChange={(e, index = i) => onChange(e, index)}
                                        />
                                    </div>
                                    <div className="from-group col-md-3">
                                        <label style={styles.label} for="inputState">
                                            Quantity
                                        </label>
                                        <input
                                            class="form-control"
                                            placeholder="Enter Extra Quantity"
                                            type="number"
                                            min="1"
                                            name="measurement_quantity"
                                            value={inputs[i].measurement_quantity}
                                            onChange={(e, index = i) => onChange(e, index)}
                                        />
                                    </div>
                                    <div className="from-group col-md-3">
                                        <label style={styles.label} for="inputState">
                                            Measurement Type
                                        </label>
                                        <select
                                            name="measurement_type"
                                            value={inputs[i].measurement_type}
                                            onChange={(e, index = i) => onChange(e, index)}
                                            id="inputState"
                                            class="form-control"
                                            style={{fontSize: '0.7em'}}>
                                            <option hidden selected>
                                                Choose
                                            </option>
                                            <option value="g">Gram</option>
                                            <option value="kg">Kilogram</option>
                                            <option value="cube">Cube</option>
                                            <option value="piece">Piece</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <hr />
                      
                        </div>
                    ))}
                 {errors.errors && <p style={styles.formError}>{errors.errors.meal_extras}</p>}

                    <div className="from-group col-md-12 text-center mt-4">
                        <button className="btn btn-style" onClick={addNewExtraData}>
                            Add More Meal Extras
                        </button>
                    </div>
                    <div className="from-group col-md-12">
                        <label style={styles.label} for="inputState">
                            Description
                        </label>
                        <ReactQuill
                            theme="snow"
                            name="description"
                            value={description}
                            onChange={onDescriptionChange}
                            row="3"
                            style={{height: '300px', marginBottom: '100px'}}
                        />
                        {errors.errors && <p style={styles.formError}>{errors.errors.description}</p>}
                    </div>
                    <div class="form-group">
                        <label for="exampleFormControlFile1" style={styles.label}>
                            Add Meal Image
                        </label>
                        <input
                            type="file"
                            class="form-control-file"
                            name="image"
                            accept="image/png, image/jpeg"
                            onChange={e => onChangeImageHandler(e)}
                        />
                        {errors.errors && <p style={styles.formError}>{errors.errors.image}</p>}
                    </div>
                </div>
                <div className="from-group col-md-12  mt-4 mb-5">
                    <button
                        className="btn btn-lg btn-block"
                        style={{backgroundColor: '#ff7417', color: 'white'}}
                        type="submit"
                        onClick={onSubmitHandle}>
                        Submit
                    </button>
                </div>
            </form>
        </div>
    );
};

const styles = {
    label: {
        fontSize: '0.9em',
        fontWeight: 'bold'
    },
    formError: {
        fontSize: '0.7em',
        color: 'red',
        marginTop: '10px'
    }
};

export default CreateMeals;
