import React from 'react';

export default function EditProfile() {
    return (
        <div className="user-dashboard mt-5 container">
            <div class="form-group">
                <label for="exampleInputEmail1" style={{fontSize: '0.7em', fontWeight: 'bold'}}>
                    First Name
                </label>
                <input type="email" class="form-control" placeholder="Firstname" />
            </div>
            <div class="form-group">
                <label for="exampleInputEmail1" style={{fontSize: '0.7em', fontWeight: 'bold'}}>
                    Last Nmme
                </label>
                <input type="email" class="form-control" placeholder="Lastname" />
            </div>
            <div class="custom-file">
                <input type="file" class="custom-file-input" id="customFile" />
                <label class="custom-file-label" for="customFile" style={{fontSize: '1em', opacity: '0.8'}}>
                    Choose Profile Avatar
                </label>
            </div>
            <div class="form-group mt-4">
                <button type="button" style={{backgroundColor: "#ff7417", color: "white"}} class="btn btn-warning">
                Update
                </button>
            </div>
        </div>
    );
}
