
import {useEffect, useState} from 'react';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';
import InfiniteScroll from 'react-infinite-scroll-component';
import ViewMealDisable from '../ReuseableCompononts/ViewMealDisable';



export default function BlockedMeals(props) {
    const [mealData, setMealData] = useState([]);
    const [meals, setMeals] = useState([]);
    const [loading, setLoading] = useState();
    const [nextPageUrl, setNextPageUrl] = useState('');

    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const nextData = () => {
        setLoading(false);
        axios
            .get(nextPageUrl, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setMeals(res.data);
                setNextPageUrl(res.data.meals.next_page_url);
                console.log('called next page: ', nextPageUrl);
                setMealData(mealData.concat(...res.data.meals.data));
            });
    };

    const loadMealData = () => {
        setLoading(true);
        axios
            .get('https://server.wakafoods.com/api/admin/meal/list/unavailable', {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setMeals(res.data.meals.data);
                console.log(res.data.meals.data);
                setMealData(res.data.meals.data);
                setNextPageUrl(res.data.meals.next_page_url);
                setLoading(false);
                // if(res.data.products.data.length < 1){
                // 	setStatus('No Ad')
                // }
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };

    useEffect(() => {
        loadMealData();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {mealData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No Blocked Meal Found</b>
                    </p>
                </div>
            ) : (
                <div class="table-responsive">
                    <table class="table">
                        <thead class="thead-light" style={{fontSize: '0.7em', width: '50px'}}>
                            <tr>
                                <th>Name</th>
                                <th>Price</th>
                                <th>Measurement Quantity</th>
                                <th>Measurement Type</th>
                                <th>Action</th>
                            </tr>
                        </thead>
                        <tbody style={{fontSize: '0.7em', width: '50px'}}>
                            {mealData.map(data => <ViewMealDisable data={data} {...props} loadMealData={loadMealData} />)}
                        </tbody>
                        <InfiniteScroll
                            dataLength={mealData.length}
                            next={nextData}
                            hasMore={meals.current_page !== meals.last_page ? true : false}
                            loader={<h4 style={{textAlign: 'center', color: 'gray'}}>Loading...</h4>}
                            endMessage={<p style={{textAlign: 'center'}} />}
                        />
                    </table>
                </div>
            )}
        </div>
    );
}