import React, {useState, useEffect} from 'react';
import {GiCancel} from 'react-icons/gi';
import {AiOutlineEdit} from 'react-icons/ai';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';
import {Link} from "react-router-dom"

export default function CategoryList(props) {
    const [categoryData, setCategoryData] = useState([]);
    const [loading, setLoading] = useState();

    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const loadCategory = () => {
        setLoading(true);
        axios
            .get('https://server.wakafoods.com/api/admin/category/list', {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setCategoryData(res.data.categories);
                setLoading(false);
            })
            .catch(error => {
                console.log(error);
                setLoading(false);
            });
    };
    useEffect(() => {
        loadCategory();
    }, []);
    return (
        <div>
            {loading && (
                <div style={{height: '100vh', width: '100%'}}>
                    <Preloader />
                </div>
            )}
            {categoryData.length < 1 ? (
                <div style={{marginTop: '10%'}}>
                    <p className="text-center">
                        <b>No Category Found</b>
                    </p>
                </div>
            ) : (
                <div class="table-responsive">
                    <table class="table">
                        <thead class="thead-light" style={{fontSize: '0.7em', width: '50px'}}>
                            <tr>
                                <th>Name</th>
                                <th>Meal </th>
                                <th>Sub-Category</th>
                                <th>Action</th>
                            </tr>
                        </thead>
                        <tbody style={{fontSize: '0.7em', width: '50px'}}>
                            {categoryData.map(data => (
                                <tr key={data.id}>
                                    <td>{data.name}</td>
                                    <td>{data.meals_count}</td>
                                    <td>{data.subcategories_count}</td>
                                    <td>
                                        <div class="btn-group btn-group-sm" role="group" aria-label="...">
                                            <Link
                                            style={{ color: 'inherit', textDecoration: 'inherit'}}
                                                to={{
                                                    pathname: `/edit-category/${data.slug}`,
                                                    state: data
                                                }}>
                                                <button
                                                    type="button"
                                                    className="btn btn-success btn-sm"
                                                    data-toggle="tooltip"
                                                    data-placement="top"
                                                    title="Edit category">
                                                    <AiOutlineEdit />
                                                </button>
                                            </Link>
                                            {/* <button
                                                type="button"
                                                className="btn btn-danger  btn-sm"
                                                data-toggle="tooltip"
                                                data-placement="top"
                                                title="Delete category">
                                                <GiCancel />
                                            </button> */}
                                        </div>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            )}
        </div>
    );
}
