import {useState, useEffect} from 'react';
import axios from 'axios';
import {useSelector} from 'react-redux';
import Preloader from '../ReuseableCompononts/Preloader';
import SweetAlert from 'sweetalert2-react';

export default function CreateLocation(props) {
    const [location, setLocation] = useState({
        name: ''
    });

    const [loading, setLoading] = useState();
    const [Successmessage, setSuccessmessage] = useState({
        status: false,
        message: ''
    });
    const [Errormessage, setErrorMessage] = useState();
    const [ErrorName, setErrorName] = useState();
    const [pickup, setPickup] = useState(false);
    const [delivery, setDelivery] = useState(false);

    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const {name} = location;
    const onChangeHandler = e => {
        setLocation({...location, [e.target.name]: e.target.value});
    };

    console.log(pickup);
    const onSubmitHandle = e => {
        e.preventDefault();
        const payload = new FormData();
        payload.append('name', location.name);
        payload.append('pickup_available', pickup?1:0);
        payload.append('delivery_available', delivery?1:0);
        setLoading(true);
        axios
            .post('https://server.wakafoods.com/api/admin/place/new', payload, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                setSuccessmessage({
                    status: true,
                    message: res.data.message
                });
                setLoading(false);
                setTimeout(() => {
                    props.history.push('/active-location-list');
                }, 2000);
            })
            .catch(error => {
                setErrorMessage(error.response.data.message);
                setErrorName(error.response.data.errors.name);
                console.log(Errormessage);
                setLoading(false);
            });
    };

    return (
        <div className="cat-subcat container">
            {loading ? <Preloader /> : null}

            {Successmessage.status ? (
                <SweetAlert
                    show={() =>
                        setSuccessmessage({
                            ...Successmessage,
                            status: true
                        })}
                    title="Success message"
                    text={Successmessage.message}
                    onConfirm={() =>
                        setSuccessmessage({
                            ...Successmessage,
                            status: false
                        })}
                />
            ) : null}

            <form style={{marginTop: '30%'}}>
                <div classsName="form-row">
                    <div class="form-group col-md-12">
                        <label style={styles.label} for="inputCity">
                            Location Name
                        </label>
                        <input
                            class="form-control"
                            type="text"
                            placeholder="Enter Location Name"
                            name="name"
                            value={name}
                            onChange={e => onChangeHandler(e)}
                        />
                        <p style={styles.formError}>
                            {Errormessage !== undefined ? `${Errormessage} ${ErrorName}` : ''}
                        </p>
                    </div>

                    <div style={{marginLeft: '30px'}} class="form-group col-md-12">
                        <input
                            class="form-check-input"
                            onChange={() => setPickup(!pickup)}
                            name="pickup_available"
                            checked={pickup}
                            type="checkbox"
                            id="pickup"
                        />
                        <label style={styles.label} class="form-check-label" for="pickup_available">
                            Enable for Pickup?
                        </label>
                        <p style={styles.formError}>
                            {Errormessage !== undefined ? `${Errormessage} ${ErrorName}` : ''}
                        </p>
                    </div>

                    <div style={{marginLeft: '30px'}} class="form-group col-md-12">
                        <input
                            class="form-check-input"
                            onChange={() => setDelivery(!delivery)}
                            name="delivery_available"
                            checked={delivery}
                            type="checkbox"
                            id="delivery"
                        />
                        <label style={styles.label} class="form-check-label" for="delivery_available">
                            Enable for Delivery?
                        </label>
                        <p style={styles.formError}>
                            {Errormessage !== undefined ? `${Errormessage} ${ErrorName}` : ''}
                        </p>
                    </div>

                    <div class="form-group col-md-12">
                        <button
                            className="btn btn-sm btn-block"
                            style={{backgroundColor: '#ff7417', color: 'white'}}
                            type="submit"
                            onClick={onSubmitHandle}>
                            Create
                        </button>
                    </div>
                </div>
            </form>
        </div>
    );
}

const styles = {
    label: {
        fontSize: '0.9em',
        fontWeight: 'bold'
    },
    formError: {
        fontSize: '0.7em',
        color: 'red',
        marginTop: '10px',
        fontWeight: 'bold'
    }
};
