import React from 'react';
import Pic from '../../images/pic.jpg';
import {GiCancel} from 'react-icons/gi';
import {FaRegUserCircle} from 'react-icons/fa';
import {IoMdCheckmarkCircle} from "react-icons/io"
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import axios from 'axios';
import {useSelector} from 'react-redux';

export default function PendingDmanView(props) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    //======USER GLOBAL STATE FROM REDUX
    const userSignin = useSelector(state => state.userSignin);
    const {user} = userSignin;

    const approveDman = () => {
        axios
            .get(`https://server.wakafoods.com/api/admin/dispatcher/activate/${props.data.code}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                props.loadDispatcherData();
            })
            .catch(error => {
                console.log(error);
            });
    };
    const deleteDman = () => {
        axios
            .get(`https://server.wakafoods.com/api/admin/dispatcher/delete/${props.data.code}`, {
                headers: {
                    Authorization: `Bearer ${user}`,
                    'Content-Type': 'application/json',
                    Accept: 'application/json'
                }
            })
            .then(res => {
                props.loadDspatcherData();
            })
            .catch(error => {
                console.log(error);
            });
    };

    return (
        <tr key={props.data.id}>
            <td>{props.data.code}</td>
            <td>{props.data.name}</td>
            <td>{props.data.place.name}</td>
            <td>{props.data.type}</td>
            <td>{props.data.deliveries_count}</td>
            <td>
                <div class="btn-group btn-group-sm" role="group" aria-label="...">
                    <button
                        type="button"
                        className="btn btn-primary"
                        data-toggle="modal"
                        data-tooltip="tooltip"
                        data-target="#userProfileModal"
                        data-placement="top"
                        onClick={handleClickOpen}
                        title="View User">
                        <FaRegUserCircle />
                    </button>
                    <button
                        type="button"
                        className="btn btn-success"
                        data-toggle="tooltip"
                        data-placement="top"
                        onClick={() => approveDman()}
                        title="Approve Delivery Man">
                        <IoMdCheckmarkCircle />
                    </button>
                    <button
                        type="button"
                        className="btn btn-danger"
                        data-toggle="tooltip"
                        data-placement="top"
                        onClick={() => deleteDman()}
                        title="Block Delivery Man">
                        <GiCancel />
                    </button>
                </div>
            </td>

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description">
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <div className="container-lg ">
                            <div className="block text-center mb-5">
                                <img
                                    src={props.data.avatar}
                                    className="rounded-circle"
                                    style={{height: '200px', width: '200px', objectFit: 'cover'}}
                                />
                            </div>
                            <p className="mt-3" style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>Name</b>
                                </span>{' '}
                                {props.data.name}
                            </p>
                            <p style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>Code</b>
                                </span>
                                {props.data.code}
                            </p>
                            <p style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>Type</b>
                                </span>
                                {props.data.type}
                            </p>
                            <p style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>Email</b>
                                </span>
                                {props.data.email}
                            </p>
                            <p style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>Phone</b>
                                </span>
                                {props.data.phone}
                            </p>
                            <p style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>Address</b>
                                </span>
                                {props.data.address}
                            </p>
                            <p style={{color: 'black', fontSize: '0.7em'}}>
                                <span className="mr-2">
                                    <b>State</b>
                                </span>
                                {props.data.place.name}
                            </p>
                            
                        </div>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button
                        style={{color: 'white', backgroundColor: '#ff7417'}}
                        onClick={handleClose}
                        color="primary"
                        autoFocus>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </tr>
    );
}
