import "bootstrap/dist/css/bootstrap.min.css";
import './App.css';
import HeaderNav from "./AppComponents/Navigation/HeaderNav"
import Dashboard from "./AppComponents/User/Dashboard"
import  NewOderList from "./AppComponents/User/NewOderList"
import ConfirmedOrderList from "./AppComponents/User/ConfirmedOrderList"
import DeliveryOderList from "./AppComponents/User/DeliveryOderList"
import CompletedOrderList from "./AppComponents/User/CompletedOrderList"
import CanceledOderList from "./AppComponents/User/CanceledOderList"
import EditProfile from "./AppComponents/User/EditProfile"
import ActiveDeliveryManList from "./AppComponents/User/ActiveDeliveryManList"
import BlockedDeliveryManList from "./AppComponents/User/BlockedDeliveryManList"
import PendingDeliveryManList from "./AppComponents/User/PendingDeliveryManList"
import ActiveUserList from './AppComponents/User/ActiveUserList'
import BlockedUserList from "./AppComponents/User/BlockedUserList"
import ActiveMeals from "./AppComponents/User/ActiveMeals"
import BlockedMeals from "./AppComponents/User/BlockedMeals"
import CategoryList from "./AppComponents/User/CategoryList"
import SubCategory from "./AppComponents/User/SubCategory"
import CreateMeals from "./AppComponents/User/CreateMeals"
import CreateCategory from "./AppComponents/User/CreateCategory";
import CreateSubCategory from "./AppComponents/User/CreateSubCategory"
import Login from "./AppComponents/Auth/Login"
import EditCategory from "./AppComponents/User/EditCategory"
import EditSubCategory from "./AppComponents/User/EditSubCategory"
import EditMeal from "./AppComponents/User/EditMeal"
import ActiveLocationList from "./AppComponents/User/ActiveLocationList"
import BlockedLocationList from "./AppComponents/User/BlockedLocationList"
import PasswordChange from "./AppComponents/User/PasswordChange"
import CreateLocation from "./AppComponents/User/CreateLocation"
import EditLocation from "./AppComponents/User/EditLocation"
import ActiveChef from "./AppComponents/User/ActiveChef"
import BlockedChef from "./AppComponents/User/BlockedChef"
import PendingReservation from "./AppComponents/User/PendingReservation"
import ApprovedReservation from "./AppComponents/User/ApprovedReservation"
import CancelledReservation from "./AppComponents/User/CancelledReservation"
import  InKitchenOrderList from "./AppComponents/User/InKitchenOrderList"
import AlmostReadyOrderList from "./AppComponents/User/AlmostReadyOrderList"
import PreparedCompeletedOrderList from "./AppComponents/User/PreparedCompeletedOrderList"
import  ReOcurringOderList from "./AppComponents/User/ReOcurringOderList"


import PrivateRoute from "./PrivateRoute"
import {
	BrowserRouter as Router,
	Route,
	Switch,

} from "react-router-dom";
import { Provider } from "react-redux";
import store from "./Redux/store"




function App(props) {
  return (
    <div className="App">
         <Provider store={store}>
    

     <Router>
     <HeaderNav/>
     <Route  {...props} exact path="/" component={Login}/>
	<Switch>
   
    <PrivateRoute {...props} exact path="/dashboard" component={Dashboard}/>
    <PrivateRoute {...props} exact path="/new-order-list" component={NewOderList}/>
    <PrivateRoute {...props} exact path="/processing-order-list" component={ConfirmedOrderList}/>
    <PrivateRoute {...props} exact path="/delivery-order-list" component={DeliveryOderList}/>
    <PrivateRoute {...props} exact path="/completed-order-list" component={CompletedOrderList}/>
    <PrivateRoute {...props} exact path="/canceled-order-list" component={CanceledOderList}/>
    <PrivateRoute {...props} exact path="/edit-profile" component={EditProfile}/>
    <PrivateRoute {...props} exact path="/active-delivery-man-list" component={ActiveDeliveryManList}/>
    <PrivateRoute {...props} exact path="/blocked-delivery-man-list" component={BlockedDeliveryManList}/>
    <PrivateRoute {...props} exact path="/pending-delivery-man-list" component={PendingDeliveryManList}/>
    <PrivateRoute {...props} exact path="/active-user-list" component={ActiveUserList}/>
    <PrivateRoute {...props} exact path="/blocked-user-list" component={BlockedUserList}/>
    <PrivateRoute {...props} exact path="/active-meal-list" component={ActiveMeals}/>
    <PrivateRoute {...props} exact path="/blocked-meal-list" component={BlockedMeals}/>
    <PrivateRoute {...props} exact path="/category-list" component={CategoryList}/>
    <PrivateRoute {...props} exact path="/sub-category-list" component={SubCategory}/>
    <PrivateRoute {...props} exact path="/create-meals" component={CreateMeals}/>
    <PrivateRoute {...props} exact path="/create-category" component={CreateCategory}/>
    <PrivateRoute {...props} exact path="/create-category" component={CreateCategory}/>
    <PrivateRoute {...props} exact path="/create-sub-category" component={CreateSubCategory }/>
    <PrivateRoute {...props} exact path="/edit-category/:slug" component={EditCategory}/>
    <PrivateRoute {...props} exact path="/edit-sub-category/:slug" component={EditSubCategory}/>
    <PrivateRoute {...props} exact path="/edit-meal/:slug" component={EditMeal}/>
    <PrivateRoute {...props} exact path="/active-location-list" component={ActiveLocationList}/>
    <PrivateRoute {...props} exact path="/blocked-location-list" component={BlockedLocationList}/>
    <PrivateRoute {...props} exact path="/change-password" component={PasswordChange}/>
    <PrivateRoute {...props} exact path="/create-location" component={CreateLocation}/>
    <PrivateRoute {...props} exact path="/edit-location/:slug" component={EditLocation}/>
    <PrivateRoute {...props} exact path="/active-chef" component={ActiveChef}/>
    <PrivateRoute {...props} exact path="/blocked-chef" component={BlockedChef}/>
    <PrivateRoute {...props} exact path="/pending-reservation" component={PendingReservation}/>
    <PrivateRoute {...props} exact path="/approved-reservation" component={ApprovedReservation}/>
    <PrivateRoute {...props} exact path="/cancelled-reservation" component={  CancelledReservation}/>
    <PrivateRoute {...props} exact path="/inkitchen-order-list" component={InKitchenOrderList}/>
    <PrivateRoute {...props} exact path="/almostready-order-list" component={AlmostReadyOrderList}/>
    <PrivateRoute {...props} exact path="/prepeared-compeleted-order-list" component={PreparedCompeletedOrderList}/>
    <PrivateRoute {...props} exact path="/future-order-list" component={ReOcurringOderList}/>
    
 
   
  </Switch>
  </Router>
  </Provider>
    </div>
  );
}

export default App;
